@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&family=Zen+Antique&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #242424;
    --color-bg-variant: #2f4646;
    --color-primary: #76c5bc;
    --color-primary-variant: #c7519b;
    --color-white: white;
    --color-light: #d5d5d5;

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body {
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    /* background-image: url(../src/Assets/texture-1.png); */
}


/******************* general styles ********************/
.container{
    width: var(--container-width-lg);
    margin: 0 auto;
}

b {
    font-weight: 600;
    color: var(--color-primary);
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5 rem;
}

section {
    margin-top: 8rem;
}

section > h2, section > h5{
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition)
}

a:hover{
    color: var(--color-white);
}

.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover{
    background: var(--color-white);
    color: var(--color-bg);
    border: transparent;
}

.btn-primary{
    background: var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/************************* Media queries (medium devices) ******************************/
@media screen and (max-width: 1024px){
    .container{
        width: var(--container-width-md);
    }
    section {
        margin-top: 6rem;
    }
}
/************************* Media queries (small devices) ******************************/
@media screen and (max-width: 600px){
    .container{
        width: var(--container-width-sm);
    }
    section > h2 {
        margin-top: 6rem;
    }
}