header {
    height: 115vh;
    padding-top: 3rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
    max-width: 40%;
}

/***************** CTA *******************/
.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/***************** HEADER SOCIALS *******************/
.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: fixed;
    left: 2rem;
    bottom: 0rem;
}

.header__socials::after{
    content: '';
    width: 1px;
    height: 4rem;
    background: var(--color-primary);
}

/***************** ME *******************/
.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/***************** SCROLL DOWN *******************/
.scroll__down{
    position: absolute;
    right: -17rem;
    bottom: 10rem;
    transform: rotate(90deg); 
    font-weight: 300;
    font-size: 1.1rem;
}

/************************* Media queries (medium devices) ******************************/
@media screen and (max-width: 1024px){
    header{
        height: 68vh;
    }
}
/************************* Media queries (small devices) ******************************/
@media screen and (max-width: 600px){
    header{
        height: 100vh;
    }
    .header__socials, .scroll__down{
        display: none;
    }
}